.section-1{
     width: 100%;
    display: block;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.section-1 .img-background {
    position: absolute;
    top: 0; left: 0;
    z-index: -5;
    height: 100%; width: 100%;
    background-image: url("../../public/img/justice-woman.png");
    transform: scaleX(-1);
    background-repeat: no-repeat;
    background-size: 120%;
    margin-top: 6rem;
    filter: opacity(34%);
    
}
.section-1 .logo {
    width: 100%; height: auto;
}
.section-1 .logo img{
    max-width: 100%;
    height: 12rem;
}

.section-1 .banner-1 {
    padding: 6rem 0.5rem 0;

    width: 100%; height: 100vh;
    display: flex; flex-direction: column;
    align-items: center;
   
    
}
.section-1 .banner-1 .icons {
    width: 100%; height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 8rem;
}
.section-1 .banner-1 .icons .icon-container img{
    max-width: 4rem;
}

.section-1 .banner-1 .title-1 {
    margin: 0 1.5rem;
    display: none;
    margin-bottom: 1rem;
}
.section-1 .banner-1 .title-1 h2 {
    font-family: 'Big Shoulders Display', cursive;
    color: #ecebeb;
    font-weight: 300;
    line-height: 0.9;
    font-size: 64px;
    padding: 0 0.5rem;
}
.section-1 .banner-1 .hr {
    width: 80%; 
    margin-left: auto; margin-right: auto;
    margin: 0 1rem;
    height: 3px;
    background-color: #F25D40;
    margin-bottom: 1rem;
}

.section-1 .banner-1 .arrow-icon-link {
    text-decoration: none;
    color: white;
}
.section-1 .banner-1 .arrow-icon-link .arrow-icon {
    display: none;
    font-size: 3rem;
}
.section-1 .banner-1 .arrow-icon:hover {
    font-size: 3.2rem;
}


.section-1 .banner-2 {
    display: none;
    height: 120vh;
    padding: 8rem 0.5rem 0;
    margin-top: 8rem;
    text-align: center;
    background-color: rgb(228, 220, 206);
}
.section-1 .banner-2 .info-1 p{
    height: 25rem;
    font-size: 24px;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 1.2px;
    padding: 0 1rem;
    color: rgb(17, 17, 17);

}

@media only screen and (min-width: 1080px) {
    .section-1 .img-background {
        position: absolute;
        top: 0; left: 0;
        z-index: -5;
        height: 100%; width: 100%;
        background-image: url("../../public/img/justice-woman-desk.png");
        transform: scaleX(1);
        background-repeat: no-repeat;
        background-size: 80%;
    
        filter: opacity(25%);
    
    }
}